* {
  box-sizing: border-box;
  margin: 0;  
  padding: 0; 
  scroll-behavior: smooth;
  font-family: var(--font-family);
}

body {
  background: var(--color-bg);
  height: 100%;
}

a {
  color: unset;
  text-decoration: none;
}

button {
  line-height: 24px;
  height: 52px;
  border: 0px;
  font-size: 18px;
  font-weight: 500;
  cursor: pointer;
}
  
h1 { 
  font-size: 70px;
  line-height: 80px;
  letter-spacing: -4px;
  color: #151515;
  padding-bottom: 20px;
  margin: 0;
}

h2 {
  font-size: 60px;
  letter-spacing: -3px;
  color: #151515;
  padding-bottom: 20px;
  margin: 0;
}

h3 {
  font-size: 28px;
  letter-spacing: -1.4px;
  color: #151515;
  padding-bottom: 16px;
  margin: 0;
}