@font-face {
    font-family: 'NeueMachina-Regular';
    src: url(./assets/fonts/NeueMachina-Regular.woff2) format('woff2'),
        url(./assets/fonts/NeueMachina-Regular.woff) format('woff');
  }
  
  @font-face {
    font-family: 'NeueMachina-Ultrabold';
    src: url(./assets/fonts/NeueMachina-Ultrabold.woff2) format('woff2'),
        url(./assets/fonts/NeueMachina-Ultrabold.woff) format('woff');
  }

:root {
    --color-bg: #f0f0f0;
    --font-family: 'NeueMachina-Regular';

    max-width: 1512px;
    vertical-align: middle;
    margin: auto;
}

